
@import "assets/css/reset.css";
body {
		background: url('assets/img/bg.jpg') #7DE6F3 no-repeat;
		/*background-attachment: fixed;*/
		background-size: 100% auto;
}
#app {
		height: 100%;
		width: 100%;
}
.boxRight {
		width: 300px;
		position: fixed;
		right: 0;
		top: 0;
		background: rgba(255, 255, 255, 0.3);
		-webkit-box-shadow:rgba(254, 87, 87, 0.2) -1px 0px 10px;
		        box-shadow:rgba(254, 87, 87, 0.2) -1px 0px 10px;
		height: 100%;
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
		-webkit-transition: all 0.5s;
		transition: all 0.5s;
}
.boxLeft {
		position: relative;
		overflow: hidden;
		width: 100%;
		-webkit-transition: all 0.5s;
		transition: all 0.5s;
		min-height: 1030px;
		border-top: 1px solid transparent;
}
.boxLeft.active {
		width: calc(100% - 300px);
		-webkit-transition: all 0.5s;
		transition: all 0.5s;
}
.boxRight.active {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
}
.sidebar-toggle {
		position: fixed;
		right: 30px;
		bottom: 45px;
		width: 14px;
		height: 14px;
		padding: 7px;
		background: #db7093;
		line-height: 0;
		z-index: 1050;
		cursor: pointer;
		border-radius: 50%;
}
.sidebar-toggle-line {
		position: relative;
		display: inline-block;
		vertical-align: top;
		height: 2px;
		width: 100%;
		background: #fff;
		margin-top: 3px;
		opacity: 1;
		-webkit-transition: all 0.3s;
		transition: all 0.3s;
}
.sidebar-toggle-line:first-child {
		margin-top: 1px;
}
.sidebar-toggle-line-first.active {
		width: 50%;
		top: 2px;
		-webkit-transform: rotateZ(-45deg);
		        transform: rotateZ(-45deg);
		opacity: 1;
		left: 0px;
}
.sidebar-toggle-line-last.active {
		width: 50%;
		top: -2px;
		-webkit-transform: rotateZ(45deg);
		        transform: rotateZ(45deg);
		opacity: 1;
		left: 0px;
}
.sidebar-toggle-line-first.active2 {
		width: 100%;
		top: 5px;
		-webkit-transform: rotateZ(-45deg);
		        transform: rotateZ(-45deg);
		opacity: 1;
		left: 0px;
}
.sidebar-toggle-line-middle.active2 {
		opacity: 0;
}
.sidebar-toggle-line-last.active2 {
		width: 100%;
		top: -5px;
		-webkit-transform: rotateZ(45deg);
		        transform: rotateZ(45deg);
		opacity: 1;
		left: 0px;
}
.routerVue {
		position: relative;
		top: 0;
		width: 1030px;
		margin: 125px auto 70px;
		z-index: 99;
}
.nav {
		width: 100%;
		height: 40px;
		position: absolute;
		top: 160px;
		z-index: 99;
		font-size: 14px;
		background: -webkit-gradient(linear, left top, right top, from(rgba(255, 123, 173, 0)), color-stop(20%, rgba(124, 189, 253, 0.6)), color-stop(80%, rgba(124, 189, 253, 0.6)), to(rgba(255, 123, 173, 0)));
		background: linear-gradient(to right, rgba(255, 123, 173, 0) 0%, rgba(124, 189, 253, 0.6) 20%, rgba(124, 189, 253, 0.6) 80%, rgba(255, 123, 173, 0) 100%);
		/*background: linear-gradient(to right,rgba(255, 167, 73, 0.2) 0%,rgb(255, 167, 73) 20%,rgb(255, 167, 73) 80%,rgba(255, 167, 73, 0.2) 100%);*/
		-webkit-animation: headMove 1s ease-out;
		        animation: headMove 1s ease-out;
}

	/*.nav.active {
		position: fixed;
		width: calc(100% - 300px);
		z-index: 999;
		top: 0;
		background: linear-gradient(to right, rgba(255, 123, 173, 0) 0%, rgba(124, 189, 253, 1) 20%, rgba(124, 189, 253, 1) 80%, rgba(255, 123, 173, 0) 100%);
		overflow: hidden;
	}*/
.nav ul {
		width: 1030px;
		margin: 0 auto;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-pack: justify;
		    -ms-flex-pack: justify;
		        justify-content: space-between;
}
.nav ul li {
		width: 100px;
		height: 40px;
		line-height: 40px;
		text-align: center;
		display: inline-block;
		color: #fff;
		cursor: pointer;
}
#canvas {
		width: 100%;
		height: 100%;
		overflow: hidden;
		position: fixed;
		top: 0;
		left: 0;
		pointer-events: none;
		z-index: 100001;
}
