@-webkit-keyframes colorChange {
0%,
		100% {
			color: #00fffc;
			top:-7px;
}
10% {
			color: #00ffa2;
}
20% {
			color: #66ff00;
			top:7px;
}
30% {
			color: #90ff00;
}
40% {
			color: #eaff00;
}
50% {
			color: #ffc000;
			top:-7px;
}
60% {
			color: #ff7200;
}
70% {
			color: #ff2a00;
}
80% {
			color: #ff0078;
			top:7px;
}
90% {
			color: #f000ff;
}
}
@keyframes colorChange {
0%,
		100% {
			color: #00fffc;
			top:-7px;
}
10% {
			color: #00ffa2;
}
20% {
			color: #66ff00;
			top:7px;
}
30% {
			color: #90ff00;
}
40% {
			color: #eaff00;
}
50% {
			color: #ffc000;
			top:-7px;
}
60% {
			color: #ff7200;
}
70% {
			color: #ff2a00;
}
80% {
			color: #ff0078;
			top:7px;
}
90% {
			color: #f000ff;
}
}
@-webkit-keyframes headMove {
0%{
			-webkit-transform: translateY(-20px);
			        transform: translateY(-20px);
			opacity: 0;
}
100% {
			-webkit-transform: translateY(0);
			        transform: translateY(0);
			opacity: 1;
}
}
@keyframes headMove {
0%{
			-webkit-transform: translateY(-20px);
			        transform: translateY(-20px);
			opacity: 0;
}
100% {
			-webkit-transform: translateY(0);
			        transform: translateY(0);
			opacity: 1;
}
}
.canvasBox {
		width: 1000px;
		height: 100px;
		margin: 0 auto;
}
.head {
		width: 1030px;
		height: 80px;
		margin: 30px auto 0;
		position: relative;
		/*z-index: 999;*/
}
.headerTop {
		color: #fff;
		/*text-align: center;*/
		font-size: 16px;
		text-shadow: #fe5757 1px 1px 3px;
		position: relative;
}
.headerTop span {
		display: inline-block;
		position: relative;
		font-size: 12px;
		top: 25px;
		cursor: pointer;
}
.headerTop p {
		position: absolute;
		font-size: 12px;
		right: 90px;
		top: 0;
		letter-spacing: 1px;
		line-height: 20px;
		-webkit-animation: colorChange 6s ease-in-out infinite;
		        animation: colorChange 6s ease-in-out infinite;
		-webkit-animation-fill-mode: both;
		        animation-fill-mode: both;
		mix-blend-mode: overlay;
		cursor: pointer;
		-webkit-transition: all 0.3s;
		transition: all 0.3s;
		font-style: italic;
}
.headerTop p.active {
		-webkit-transform: translateX(-200%);
		        transform: translateX(-200%);
}
.headerTop h1 {
		font-size: 26px;
		font-weight: normal;
}
.blogName {
		color: #fff;
		position: relative;
}
.blogName:before {
		content: "";
		position: absolute;
		width: 100%;
		height: 2px;
		bottom: -5px;
		left: 0;
		background-color: #db7093;
		visibility: hidden;
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
		-webkit-transition: all 0.3s;
		transition: all 0.3s;
}
.navLink {
		display: inline-block;
		width: 100%;
		height: 100%;
		color: #fff;
		text-shadow: #fe5757 1px 1px 1px;
		-webkit-transition: all 0.5s;
		transition: all 0.5s;
		-webkit-transform: rotate(-35deg);
		        transform: rotate(-35deg);
		background: rgba(255, 255, 255, 0.4);
		border-radius: 50px 0;
}
.navLink:hover {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
		background: #ff7bad;
		font-weight: bold;
}
.nav .router-link-exact-active ,.nav .router-link-active{
		-webkit-transform: rotate(0);
		        transform: rotate(0);
		background: #ff7bad;
		font-weight: bold;
}
.blogName.router-link-exact-active {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
		background: transparent;
}
.blogName:hover::before {
		content: "";
		visibility: visible;
		-webkit-transform: scaleX(1);
		        transform: scaleX(1);
}
.footer {
		height: 40px;
		width: 100%;
		line-height: 40px;
		color: #fff;
		text-shadow: #fe5757 1px 1px 1px;
		text-align: center;
		background: rgba(255, 255, 255, 0.5);
		position: absolute;
		bottom: 0;
}
.timaAndmusic {
		position: absolute;
		z-index: 100;
		top: 205px;
		right: 0;
		width: 270px;
		-webkit-transform: translateX(10000px);
		        transform: translateX(10000px);
}
.timeEmbed {
		-webkit-box-shadow: 0 0 10px #ccc;
		        box-shadow: 0 0 10px #ccc;
		background: rgba(255,255,255,0.5);
		margin-top: 15px;
}
.musicEmbed{
		position: relative;

		width: 270px;
		height: 90px;
		-webkit-box-shadow: 0 0 10px #ccc;
		        box-shadow: 0 0 10px #ccc;
		border-radius: 3px;
		overflow: hidden;
}
.musicEmbed embed{
		position: absolute;
		top: -10px;
		left: -20px;
}
.editor{
		width: 40px;
		height: 40px;
		position: absolute;
		right: 0;
		bottom: 0;
}
.pictureBox{
		height: 100px;
		width: 100px;
		margin:100px auto 0;
		border-radius: 50%;
		overflow: hidden;
		background: url(./assets/img/headpic2.jpg) center -28px;
		background-size: 100px;
		cursor: pointer;
		-webkit-transition: all 1s;
		transition:all 1s;
}
.pictureBox:hover{
		-webkit-transform: scale(1.3) rotateZ(360deg);
		        transform: scale(1.3) rotateZ(360deg);
}
.namep{
		margin: 15px 0 25px;
		text-align: center;
		color: #ef3900;
		font-weight: bold;
}
.jianjie{
		margin-top: 10px;
		font-size: 12px;
		text-align: center;
		color: #ef7000;
}
.dictum{
		width: 240px;
		margin: 40px auto 0;
		line-height: 30px;
		font-size: 12px;
		text-indent: 2em;
		color: #ef7000;
}
@media screen and (max-width: 1280px) {
.sidebar-toggle {
        display: none;
}
body{
    	min-width: 1280px;
}
}
	


